<template>
  <div>
    <section class="msg">
      <div>
        <div class="msg-wrapper">
          <!--<p><i>*</i>{{item.label}}</p>-->
          <!--<span>{{item.detail}}</span>-->
          <p><i>*</i>储存库/标签：</p>
          <span>{{this.$route.query.repotag}}</span>
        </div>
        <div class="msg-wrapper">
          <p><i>*</i>注册表：</p>
          <span>Docker Hub</span>
        </div>
        <div class="msg-wrapper">
          <p><i>*</i>镜像创建时间：</p>
          <span>{{(new Date((this.net.dockerImage[0].Created) * 1000)).toLocaleString()}}</span>
        </div>
        <div class="msg-wrapper">
          <p><i>*</i>最新扫描时间：</p>
          <!--<span>{{this.net.json_time}}</span>-->
          <span>{{(new Date(this.net.json_time)).toLocaleString()}}</span>
        </div>
        <div class="msg-wrapper">
          <p><i>*</i>操作系统 / 版本 ：</p>
          <span>{{this.net.vulnerabilities[0].namespace}}</span>
        </div>
        <div class="msg-wrapper">
          <p><i>*</i>ubuntu / 16:01：</p>
          <span class="msg-wrapper-dockerId">{{this.net.dockerImage[0].Id}}</span>
        </div>
        <div class="msg-wrapper">
          <p><i>*</i>ubuntu / 16:01：</p>
          <span>{{this.net.dockerImage[0].Containers}}</span>
        </div>
        <div class="msg-wrapper">
          <p><i>*</i>官方镜像标志：</p>
          <span v-if="(Math.round(this.net.dockerImage[0].VirtualSize /1024/1024))<1024">
            {{Math.round(this.net.dockerImage[0].VirtualSize /1024/1024)}}MB</span>
          <span v-else>
            {{Math.round(this.net.dockerImage[0].VirtualSize /1024/1024/1024)}}GB</span>
        </div>
        <!--<p><i>*</i>官方镜像标志：</p>-->
        <!--<span class="overview-text1">镜像概览</span>-->
        <!--<span class="overview-text2" style="color:#FFFFFF"> ubuntu</span>-->
      </div>
    </section>
    <hr>
    <section class="issues">
      <h2>安全信息</h2>
      <div>
        <div class="issues-highest">
          <p>
            <i class="iconfont  icon-system-fn-warning overview-warning icon-red">
              &#xe638;</i>
            <span>{{net.level.High}}</span>
          </p>
          <p>高危漏洞</p>
        </div>
        <div class="issues-highest">
          <p>
            <i class="iconfont icon-system-fn-warning  overview-warning icon-orange">
              &#xe638;</i>
            <span>{{net.level.Medium}}</span>
          </p>
          <p>中间漏洞</p>
        </div>
        <div class="issues-highest">
          <p><i class="iconfont  icon-system-fn-warning overview-warning icon-yellow">
            &#xe638;</i>
            <span>{{net.level.Low}}</span>
          </p>
          <p>低危漏洞</p>
        </div>
        <div class="issues-highest">
          <p><i class="iconfont icon-system-fn-warning  overview-warning icon-blue">
            &#xe638;</i>
            <span>{{net.level.Negligible}}</span>
          </p>
          <p>轻微漏洞</p>
        </div>
        <div class="issues-highest">
          <p><i class="iconfont icon-system-fn-warning  overview-warning icon-black">
            &#xe638;</i>
            <span>{{net.level.Unknown}}</span>
          </p>
          <p>未知漏洞</p>
        </div>
      </div>
    </section>
  </div>
</template>

<script type="text/ecmascript-6">

  export default {
    props: ['net'],
    data() {
      return {
      }
    }
  }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  .msg
    text-align: left
    column-count: 2
    margin: 40px 0 40px 0
    .msg-wrapper
      color: #333333
      margin: 0 0 34px 0
      .msg-wrapper-dockerId
        word-break break-all
    p
      font-size: 1.3em
      display inline-block
      width: 32%
      max-width: 40%
      i
        color: #FF0808
        font-size: 1.3em
        margin: 0 4px 0 0
    span
      font-size: 1.4em
      color: #525252

  .overview-text1
    display inline-block
    color: #525252
    font-size: 12px
    padding: 10px 0px
    text-align center
    width: 120px
    background: #D9E5FE

  .overview-text2
    display: inline-block
    color: #FFFFFF
    font-size: 12px
    padding: 10px 0px
    text-align center
    width: 120px
    background: #3E7AF6
    margin: 0 0 0 -3px

  div[class^="issues"]
    display inline-block
    width: 215px
    height 75px
    margin 48px 0 40px 0
    line-height: 30px
    p
      text-align: left
      color: #808080
      font-size: 1.4em
      i
        display: inline-block
        margin: 0 20px 0 0
      span
        color: #4D4D4D
        font-size: 2.0em
        font-weight: 600
    p:last-child
      margin: 0px 0 0 58px

  .split-line
    color: #E6E6E6

  .bg-purple
    background: #F8F8F8

  .grid-content
    height: 145px;

  .media-right-title
    font-size: 1.4em
    color: #5F5F5F
    font-weight 600
    padding: 20px 35px
    display: inline-block

  .media-right-time, .media-right-size
    color: #5F5F5F
    font-size: 12px
    float: right
    padding: 20px 18px 0 0

  .media-right-size
    margin: 0 20px 0 0

  i[class^="icon-overview"]
    margin: -3px 8px 0 0
    font-size: 2.3em

  div[class^="overview-wrapper"]
    display: inline-block
    margin: 20px 25px 60px 18px
    width: 18%
    p
      text-align: center
      color: #808080
      font-size: 1.2em
      line-height: 26px
      i
        display: inline-block
        margin: -3px 10px 0 0
      span
        color: #4D4D4D
        font-size: 1.8em
        font-weight: 600
    p:last-child
      margin: 0 0 0 50px

  .overview-warning
    font-size: 2.3em

  .icon-red
    color: #FF0000

  .icon-orange
    color: #F27935

  .icon-yellow
    color: #F9BF3B

  .icon-blue
    color: #08B1D5

  .icon-black
    color: #1A1A1A
</style>



